import React from 'react';
import logo from './logo.svg';
import './App.css';

import ReactiveQR from "reactive-qr";
import QrScanner from 'qr-scanner';
QrScanner.WORKER_PATH = './qr-scanner-worker.min.js';

class App extends React.Component {
  state = {
    page : 1
  }
  videoElem;
  qrScanner;

  componentDidUpdate(prevProps, prevState) {
    if (prevState.page != this.state.page && this.state.page !== 3) {
      if (this.qrScanner) {
        this.qrScanner.destroy();
        this.qrScanner = null;
      }
    }
  }
  createScanner = () => {
    if (this.videoElem) {
      this.qrScanner = new QrScanner(this.videoElem, code => this.setState({code}));
      this.qrScanner.start()
    }
  }
  render() {
    return (
      <div className="App">
        <button onClick={() => this.setState({page : 2, code : ''})}>reactive-qr</button>
        <button onClick={() => this.setState({page : 3, code : ''})}>QrScanner</button>
        {
          this.state.page == 2 && (
            <div>
              <ReactiveQR onCode={code => this.setState({code})} />
              <span>QrCode : {this.state.code}</span>
            </div>
          )
        }
        {
          this.state.page == 3 && (
            <div>
              <video ref={e => {this.videoElem = e; this.createScanner()}}></video>
              <span>QrCode : {this.state.code}</span>
            </div>
          )
        }
      </div>
    );  
  }
  
}

export default App;
